@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper main {
  flex-grow: 1;
}

* {
  font-family: "Nunito", sans-serif;
}

.page-header nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  background: #200122;
  background: linear-gradient(to right, #6f0000, #200122);
}

/* .adjuster {
  height: 100px;
} */

.brand-name p {
  font-size: 2rem;
  margin: 0 1rem;
  font-family: "Rock Salt", cursive;
  color: #d1754f;
  display: inline-flex;
  transition: 0.3s ease-in-out;
}
.brand-name {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  color: #f2b354;
  padding: 10px;
}

.brand-name-home {
  text-decoration: none;
  color: #000;
  font-size: 2rem;
  color: inherit;
  transition: 0.3s ease-in-out;
}

.navbar-items {
  display: flex;
  justify-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: auto;
  list-style: none;
  padding-left: 0;
}

.navbar-item {
  text-decoration: none;
  color: #000;
  font-size: 1.5rem;
  padding: 15px;
  color: #fff;
  transition: 0.3s ease-in-out;
}

.navbar-item-order {
  border-radius: 50px;
  margin-left: 50px;
  background-color: #5d1e3d;
}

.active {
  color: #f2b354;
}
.navbar-item:hover,
.navbar-item:active {
  color: #f2b354;
  transition: 0.3s ease-in-out;
}

.navbar-item-order:hover {
  background-color: #5d1e3e65;
}

@media screen and (max-width: 560px) {
  .brand-name-home {
    font-size: 1.5rem;
  }
  .navbar-items {
    display: flex;
    width: 100%;
  }

  .navbar-item {
    font-size: 1.1rem;
    padding: 8px;
  }

  .navbar-item-order {
    margin: 0px;
  }

  /* .adjuster {
    height: 30vh;
  } */
} ;

.legacy {
  display: flex;
  flex-wrap: wrap;
  padding: 10%;
  color: #fff;
  min-height: 350px;
  transition: 0.5s ease;
  margin: 0;
}

.legacy:hover {
  transition: background 0.5s ease;
}

.legacy-container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  background: #000 url("/Restaurant-App/static/media/home-bg(2).5406344f.jpg") no-repeat center / cover;
  background-size: 100% 100%;
  position: relative;
}

.legacy-container:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  background-color: rgba(0, 0, 0, 0.2);
  transition: background 0.5s ease-in;
}

.legacy-container:hover::after {
  background-color: rgba(0, 0, 0, 0.6);
}

.legacy-container .legacy {
  font-size: 1.2rem;
  text-align: center;
  color: white;
  position: relative;
  max-height: 50vh;
  overflow-y: scroll;
  z-index: 1;
}
/* 
@media screen and (max-width: 800px) {
  .legacy {
    max-height: 60vh;
    overflow-y: scroll;
  }
} */

.legacy h1 {
  font-size: 3rem;
}

.main-content {
  height: auto;
  margin-bottom: 20px;
}

.menu-wrapper {
  display: flex;
  justify-content: center;
  justify-items: center;
  align-items: center;
  flex-wrap: wrap;
}

h1 {
  width: 100%;
  text-align: center;
}

.all-items {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 60vh;
  width: 70%;
  margin: 0 auto;
  height: 100%;
}

@media screen and (max-width: 700px) {
  .all-items {
    grid-template-columns: 1fr;
  }
}

.search-bar {
  display: block;
}

.order-button {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 5%;
  right: 5%;
  font-size: 30px;
  background-color: #5d1e3d;
  border-radius: 60px;
  padding: 15px;
  width: 40px;
  height: 40px;
  color: #fff;
  transition: 0.3s ease-in-out;
}

.order-button:hover {
  color: #f2b354;
  cursor: pointer;
  width: 50px;
  height: 50px;
  font-size: 40px;
  transition: 0.3s ease-in-out;
}

.divider {
  margin: 40px auto;
  width: 80%;
  border: 1px solid rgba(109, 70, 70, 0.541);
}

.search-bar-container {
  margin: 10px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  margin: 5vh 0;
  padding: 10px;
}

.search-bar {
  height: auto;
  width: 50%;
  border-radius: 30px;
  border: 1px solid #777277;
  padding: 0.5em 1em;
  font-size: 1.2em;
  font-family: "Roboto", sans-serif;
  transition: 0.5s ease-in-out;
}

.search-bar:hover,
.search-bar:focus {
  outline: transparent;
  width: 80%;
  border-color: #ccad25;
  transition: 0.5s ease-in-out;
}

.search-bar:hover ~ .search-icon,
.search-bar:focus ~ .search-icon {
  color: #ccad25;
}

.search-icon {
  /* width: 10%;
  display: flex;
  justify-content: flex-end; */
  margin-right: 10px;
  font-size: 1.5em;
  font-weight: lighter;
  color: rgba(71, 64, 64, 0.664);
}

@media screen and (max-width: 560px) {
  .search-bar {
    width: 75%;
  }

  .search-bar:hover,
  .search-bar:focus {
    width: 90%;
  }
}

.menu-item {
  border-radius: 40px;
  margin: 15px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  align-content: space-between;
  flex-grow: 1;
  flex-shrink: 1;
  justify-content: flex-start;

  background: #200122;
  background: linear-gradient(to right, #6f0000, #200122);
  transition: 0.5s;
  color: #fff;
  overflow: hidden;
}

.menu-item:hover {
  cursor: pointer;
  transition: 0.5s;
  /* transform: scale(0.9, 0.9);
  box-shadow: 10px 10px 20px 5px #74262d7e, -10px -10px 20px 5px #74262d,
    -10px 10px 20px 5px #74262d, 10px -10px 20px 5px #74262d; */
}

.menu-item:hover img {
  transition: 0.5s ease;
  transform: scale(1.1);
}

.menu-item img {
  transition: 0.5s ease;
}

.menu-item .img-class {
  height: 80%;
  width: 100%;
  overflow: hidden;
}

.img-class img {
  border-radius: 40px;
  height: 100%;
  width: 100%;
  transition: 0.5 ease;
}

.title-price-banner {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  flex-grow: 1;
  flex-wrap: wrap;
  position: relative;
  border-radius: 40px;
}

.item-title {
  font-size: 1.3rem;
  display: flex;
  white-space: nowrap;
}

.item-price {
  font-size: 1.2rem;
  margin: 0 10px;
}

.footer-bar {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-items: center;
  justify-content: space-evenly;
  align-items: center;
  height: 10%;
  background: #200122;
  background: linear-gradient(to right, #6f0000, #200122);
  color: #fff;
}

.footer-bar .icons {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 10px 0;
}

.icons a:hover {
  color: #f2b354;
  transition: 0.5s ease;
}

.footer-bar a {
  color: #fff;
  font-size: 1.5rem;
  padding: 0 20px;
  transition: 0.5s ease;
}

.copyrights {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.copyrights i {
  margin: 0 10px;
  font-weight: lighter;
}

@media screen and (max-width: 560 px) {
  .copyrights {
    width: 100%;
  }
}

